import {useEffect, useState} from "react";
import {Avatar, Grid, ListItem, ListItemAvatar, ListItemText, Paper, Skeleton} from "@mui/material";
import axios from "axios";
import Web3 from "web3";
import millify from "millify";
import fallback_img from "../images/fallback.png";


export default function PriceScroll() {


    const [tokens, setTokens] = useState([]);

    useEffect(() => {
        axios.post(
            "https://api.thegraph.com/subgraphs/name/somemoecoding/surgeswap-v2",
            {
                query: `
                    {
                      tokens(
                        where:{
                          id_not:"0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe",
                          totalLiquiditySRG_gt: 50000
                      }
                        orderBy: totalLiquiditySRG
                        orderDirection: desc
                      ){
                        id
                        name
                        symbol
                        tradeVolumeUSD
                        totalLiquiditySRG
                      }
                    }
            `
            }
        ).then(resp => {
            const {tokens} = resp.data.data;

            tokens.map(t=>t.network='bnb');

            setTokens(tokens)

            axios.post(
                "https://api.thegraph.com/subgraphs/name/somemoecoding/surgeswap-v1-eth",
                {
                    query: `
                    {
                      tokens(
                        where:{
                          id_not:"0xcD682EF09d07668d49A8103ddD65Ff54AebFbfDe",
                          totalLiquiditySRG_gt: 50000
                      }
                        orderBy: totalLiquiditySRG
                        orderDirection: desc
                      ){
                        id
                        name
                        symbol
                        tradeVolumeUSD
                        totalLiquiditySRG
                      }
                    }
            `
                }
            ).then(resp => {
                const a = resp.data.data.tokens;

                a.map(t=>t.network='eth');

                const b = [...tokens, ...a];

                console.log(b);

                b.sort((a,b) => {
                    if(parseFloat(a.totalLiquiditySRG) > parseFloat(b.totalLiquiditySRG)){
                        return -1;
                    }
                    if(parseFloat(a.totalLiquiditySRG) < parseFloat(b.totalLiquiditySRG)){
                        return 1;
                    }
                    return 0;
                })

                setTokens(b)
            })
        })
    }, [])

    return <Grid item xs={12}>
        <div style={{
            marginTop: 10,
            display: 'grid',
            gridAutoFlow: 'column',
            gap: '1rem',
            height: 'calc(135px + 1rem)',
            overflowY: 'auto',
            overscrollBehaviorX: 'contain',
            scrollSnapType: 'x mandatory'
        }}>
            {
                tokens.length === 0 && <>
                {
                    [0,1,2].map(i => {
                        return <Paper style={{scrollSnapAlign: 'start', height: 115}} key={i}>
                            <ListItem style={{
                                width: 200,
                                maxWidth: 'none',
                                objectFit: 'contain'
                            }}>
                                <ListItemAvatar>
                                    <Skeleton variant="circular" width={40} height={40} />
                                </ListItemAvatar>
                                <ListItemText
                                    primary={<Skeleton variant="text" sx={{ fontSize: '1rem' }} />}
                                    secondary={<Skeleton variant="text" sx={{ fontSize: '1rem' }} />}
                                />
                            </ListItem>
                        </Paper>
                    })
                }

                </>
            }

            {
                tokens.map(token => {
                    return <Paper style={{scrollSnapAlign: 'start', height: 125, marginTop: 25}} key={token.network+"_"+token.id}>

                        <Avatar
                            src={`https://coineus.app/assets/tokens/${token.network}/${Web3.utils.toChecksumAddress(token.id)}/logo.png`}
                            style={{
                                width: 50,
                                height: 50,
                                borderRadius: '50%',
                                marginTop: -25,
                                marginLeft: 'calc(50% - 25px)'
                            }}
                        >
                            <img src={fallback_img} />
                        </Avatar>

                        <img
                            src={`https://coineus.app/assets/tokens/${token.network}/logo.png`}
                            style={{
                                width: 20,
                                height: 20,
                                borderRadius: '50%',
                                top: -25,
                                marginLeft: 'calc(50% + 15px)',
                                position: 'relative',
                                visibility: token.id === 'native' ? 'hidden' : 'visible'
                            }}
                        />

                        <ListItem style={{
                            width: 200,
                            maxWidth: 'none',
                            objectFit: 'contain',
                            paddingTop: 0,
                            paddingBottom: 0,
                            marginTop: -25
                        }}>
                            <ListItemText
                                primary={token.symbol}
                                primaryTypographyProps={{fontWeight: 700, variant: 'h5', textAlign: 'center'}}
                            />
                        </ListItem>

                        <ListItem style={{
                            width: 200,
                            maxWidth: 'none',
                            objectFit: 'contain',
                            paddingTop: 0,
                            paddingBottom: 0
                        }}>
                            <ListItemText
                                primary={millify(token.totalLiquiditySRG) + " SRG"}
                                primaryTypographyProps={{fontWeight: 700, variant: 'h6', textAlign: 'center'}}
                            />
                        </ListItem>
                    </Paper>
                })
            }
        </div>

    </Grid>
}